<template>
    <main id="app">
        <header>
            <div class="titanfall-pop-up" v-show="titanfallSite" transition="ease-it">
                Visit the Titanfall 2 Offical Website
            </div>
            <a href="http://www.titanfall.com/" target="_blank" v-on:mouseover="showSite" v-on:mouseout="hideSite">
                <img src="./assets/logo.svg" class="logo">
            </a>
        </header>
        <section class="clockStyle">
            <p>
                {{ days }}
                <span v-text="ref_days"></span>
            </p>
            <p>
                {{ hours }}
                <span v-text="ref_hours"></span>
            </p>
            <p>
                {{ minutes }}
                <span v-text="ref_minutes"></span>
            </p>
            <p>
                {{ seconds }}
                <span v-text="ref_seconds"></span>
            </p>
        </section>
        <section>
            <iframe class="trailer" src="https://www.youtube.com/embed/hJ1r2f1Jv0Y" frameborder="0"  allowfullscreen>
            </iframe>
        </section>
        <section>
            <div class="adk-pop-up" v-show="largeImage" transition="ease-it">
                <img src="./assets/adk.png" class="adk-img-large">
            </div>
            <a href="http://www.adkgamers.com/" target="_blank" v-on:mouseover="showLargeImage" v-on:mouseout="hideLargeImage">
                <img src="./assets/adk.png" class="adk-img">
            </a>
        </section>
        <footer>
            <h1>WORLDWIDE REVEAL JUNE 12</h1>
        </footer
    </main>
</template>

<script>
export default {
     data () {
        return {
            releaseDate: '12 June 2016 00:00:01',
            difference: 0,
            days: 0,
            hours: 0,
            minutes: 0,
            seconds: 0,
            ref_days: '',
            ref_hours: '',
            ref_minutes: '',
            ref_seconds: '',
            largeImage: false,
            titanfallSite: false
        }
    },
    methods: {
        countDown() { 
            var release = new Date(this.releaseDate);
            var now = new Date();
            var timeDiff = release.getTime() - now.getTime();
            if(timeDiff <= 0) {
                clearTimeout(timer);
            }
            var seconds = Math.floor(timeDiff / 1000);
            var minutes = Math.floor(seconds / 60);
            var hours = Math.floor(minutes / 60);
            var days = Math.floor(hours / 24);
            hours %= 24;
            minutes %= 60;
            seconds %= 60;
            this.days = days;
            this.hours = hours;
            this.minutes = minutes;
            this.seconds = seconds;
            var timer = setTimeout(this.countDown,1000);

            this.ref_days = (days === 1 || days === 0) ? 'Day' : 'Days',
            this.ref_hours = (hours === 1 || hours === 0) ? 'Hour' : 'Hours',
            this.ref_minutes = (minutes === 1 || minutes === 0) ? 'Minute' : 'Minutes',
            this.ref_seconds = (seconds === 1 || seconds === 0) ? 'Second' : 'Seconds';
        },
        showLargeImage() {
            this.largeImage = true;
        },
        hideLargeImage() {
            this.largeImage = false;
        },
        showSite() {
            this.titanfallSite = true;
        },
        hideSite() {
            this.titanfallSite = false;
        }
    },
    ready() {
        this.countDown();
    }
}
</script>

<style>
    html {
        background-image: url(./assets/titanfall2.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        z-index:1;
        font-family:"Arial Black", Gadget, sans-serif;
        height: 100%;
        width: 100%;
    }
    body {
        margin:0;
        padding:0;
        height: 100%;
        width: 100%;
        background-color: rgba(0,0,0,0.3);
        z-index:100;
    }
    .logo {
        height: 110px;
        width: 600px;
        position: absolute;
        top: 20%;
        left: 10%;
    }
    .trailer {
        width: 560px;
        height: 315px;
        background-color: rgba(0,0,0,0.4);
        border-radius: 7px;
        padding: 5px 10px 5px 10px;
        position: absolute;
        top: 40%;
        left: 11%;
    }
    .clockStyle {
        width: 400px;
        height: 400px;
        background-color: rgba(0,0,0,0.3);
        border-radius: 7px;
        padding: 5px 10px 5px 10px;
        color:#0FF;
        font-size:36px;
        font-weight:bold;
        text-align: center;
        letter-spacing: 4px;
        position: absolute;
        top: 30%;
        left: 65%;
    }
    .adk-img {
        width: 75px;
        height: 75px;
        border-radius: 3px;
        position: absolute;
        bottom: 1%;
        left: 1%;
        z-index: 1000;
    }
    .adk-img-large {
        border-radius: 17px;
        position: relative;
        margin: 22px;
    }
    .adk-pop-up {
        background-color: rgba(0,0,0,0.9);
        border-radius: 17px;
        height: 300px;
        width: 300px;
        margin: 20px;
        position: absolute;
        bottom: 7%;
        left: 1%;
        z-index: 1500;
    }
    .titanfall-pop-up {
        width: 700px;
        background-color: rgba(0,0,0,0.3);
        border-radius: 7px;
        padding: 5px 10px 5px 10px;
        color:#fff;
        font-size:26px;
        font-weight:bold;
        text-align: center;
        letter-spacing: 4px;
        position: absolute;
        top: 15%;
        left: 7%;
        z-index: 2000;
    }
    .ease-it-transition {
        transition: all .7s ease;
        overflow: hidden;
    }
    .ease-it-enter, .ease-it-leave {
        opacity: 0;
    }
    footer {
        background-color: rgba(0,0,0,0.4);
        color: #fff;
        font-size:20px;
        font-weight:bold;
        text-align: center;
        height: 100px;
        width: 100%;
        position: absolute;
        bottom: 0;
    }
    footer h1 {
        padding: 20px;
        margin:0;
    }
</style>



